.item {
  margin-bottom: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #0a67a3;
}

p {
  text-align: start;
  width: 150px;
  margin: 0px;
}

.text {
  margin-left: 10px;
}

.button {
  min-width: 100px;
  font-family: inherit;
  appearance: none;
  border: 1px solid #8e0039;
  border-radius: 5px;
  background: #ffc173;
  color: #24587a;
  padding: 5px 16px;
  font-size: 1rem;
  cursor: pointer;
}

button:hover {
  color: #0a67a3;
  background: #ff8e00;
}
